.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 30px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.5s ease-in-out;
}

.nav__black {
    background-color: #111;
}

.navbar__logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.navbar__avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}